.TrackList {
    width: 50%;
    height: 950px;
    padding: .88rem;
    background-color: rgba(1, 12, 63, 0.7);
    box-shadow: 0 4px 2px 2px #000000;
}

.Tracks {
    height: 87%;
    margin-bottom: 1.5rem;
    overflow-y: scroll;
}
  
::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

.Buttons-container {
    display: flex;
    justify-content: space-around;
}

.Button {
    cursor: pointer;
    width: 9rem;
    padding: .77rem 0;
    border-radius: 54px;
    background-color: #6c41ec;
    color: #fff;
    text-align: center;
    align-items: center;
    font-size: .83rem;
    transition: background-color .25s;
    border: 0px;
    font-weight: 500;
}

.Button:hover {
    background-color: rgba(108, 65, 233, .7);
}
  
@media only screen and (max-width: 1300px) {
    .TrackList {
        width: 90%;
        margin-bottom: 2rem;
    }
}

@media only screen and (max-width: 450px) {
    .Buttons-container {
        justify-content: space-between;
    }

    .Button {
        width: 7rem;
        height: 3rem;
        border-radius: 35px;
    }
}

@media only screen and (max-width: 280px) {
    .Button {
        width: 5.3rem;
    }
}